// Learn life-cycle callbacks:
//  - [Chinese] http://docs.cocos.com/creator/manual/zh/scripting/life-cycle-callbacks.html
//  - [English] http://www.cocos2d-x.org/docs/creator/en/scripting/life-cycle-callbacks.html

cc.Class({
    extends: cc.Component,
    properties: {
        // //
        // nodeName:{
        //     default:null,
        //     type:cc.Node
        // },
        // //
        // nodePicture:{
        //     default:null,
        //     type:cc.Node
        // },
        // foo: {
        //     // ATTRIBUTES:
        //     default: null,        // The default value will be used only when the component attaching
        //                           // to a node for the first time
        //     type: cc.SpriteFrame, // optional, default is typeof default
        //     serializable: true,   // optional, default is true
        // },
        // bar: {
        //     get () {
        //         return this._bar;
        //     },
        //     set (value) {
        //         this._bar = value;
        //     }
        // },
        userNameLabel : cc.Label,
        rivalNameLabel : cc.Label,
        countDownAudio : {
            default : null,
            url     : cc.AudioClip,
        },
        clickAudio : {
            default : null,
            url     : cc.AudioClip,
        },
        backBtn     : cc.Node,
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        this.userNameLabel.string = cc.find("PebmanentNode").getComponent("UserInfo").nameUser;
        if(cc.director.isPaused){
            //
            console.log("");
            cc.director.resume();
        }
        //
        this.matchSuccess=false;
        //
        this.showCountDown = 0;
          //
        this.nSuccessTime=0;
        //
        this.startTime = 5;
        this.node.getChildByName("countDown").active = false;
        this.UserState();
        //
        var sceneArr = ["PersonsGame1","FightFigure"];
        for(let i = 0;i < sceneArr.length;i++){
            this.preLoadGameScene(sceneArr[i]);
        }
        this.progress = 0;
        this.resource = null;
        this.sceneLoad = false;
        //
        if(CC_WECHATGAME){
            var self = this;
            this.urls = [
                {url : 'res/raw-assets/Textures/cPart/tetrisbg.81dbd.png',type : 'png'},
                {url : 'res/raw-assets/Textures/bPart/pink/44.1d79a.png',type : 'png'},
                {url : 'res/raw-assets/Textures/bPart/yellow/10.b6d8f.png',type : 'png'},
                {url : 'res/raw-assets/Textures/bPart/yellow/9.44e42.png',type : 'png'},
                {url : 'res/raw-assets/Textures/bPart/yellow/11.68a0f.png',type : 'png'},
                {url : 'res/raw-assets/Textures/bPart/yellow/7.cebf4.png',type : 'png'},
                {url : 'res/raw-assets/Textures/bPart/yellow/14.7af40.png',type : 'png'},
                {url : 'res/raw-assets/Textures/bPart/yellow/13.03702.png',type : 'png'},
                {url : 'res/raw-assets/Textures/bPart/yellow/12.8986b.png',type : 'png'},
                {url : 'res/raw-assets/Textures/bPart/yellow/8.10c64.png',type : 'png'},
                {url : 'res/raw-assets/resources/Game4/-1.33262.png',type : 'png'},
                {url : 'res/raw-assets/resources/Game4/7.cebf4.png',type : 'png'},
                {url : 'res/raw-assets/resources/Game4/8.10c64.png',type : 'png'},
                {url : 'res/raw-assets/resources/Game4/9.44e42.png',type : 'png'},
                {url : 'res/raw-assets/resources/Game4/10.b6d8f.png',type : 'png'},
                {url : 'res/raw-assets/resources/Game4/11.68a0f.png',type : 'png'},
                {url : 'res/raw-assets/resources/Game4/12.8986b.png',type : 'png'},
                {url : 'res/raw-assets/resources/Game4/13.03702.png',type : 'png'},
                {url : 'res/raw-assets/resources/Game4/14.7af40.png',type : 'png'},
                {url : 'res/raw-assets/resources/picture/0.1d79a.png',type : 'png'},
                {url : 'res/raw-assets/resources/picture/1.c4498.png',type : 'png'},
                {url : 'res/raw-assets/resources/picture/2.ad5ec.png',type : 'png'},
                {url : 'res/raw-assets/resources/picture/3.8a1f4.png',type : 'png'},
                {url : 'res/raw-assets/resources/picture/4.036f4.png',type : 'png'},
                {url : 'res/raw-assets/resources/picture/5.d7fd4.png',type : 'png'},
            ];
            this.clearAll();
            cc.loader.load(self.urls,self.progressCallback.bind(self),self.completeCallback.bind(self));
        }
        //
        cc.find("PebmanentNode").getComponent("UserInfo").showAdvice("adunit-e8fb34802309ceff");
    },
    clearAll : function(){
        for(let i = 0;i < this.urls.length;i++){
            let u = this.urls[i];
            cc.loader.release(u);
        }
    },
    progressCallback : function(completedCount,totalCount,res){
        console.log("");
        console.log("",completedCount);
        console.log("",totalCount);
        this.progress = completedCount/totalCount;
        console.log("",this.progress);
        this.resource = res;
    },
    completeCallback : function(err,texture){
        if(err){
            console.log("");
        }
        console.log("texture is ",texture);
    },
    //
    preLoadGameScene : function(gameSceneName){
        cc.director.preloadScene(gameSceneName,function(){
            console.log(gameSceneName + "");
        });
    },
    //
    UserState:function(){
        //
        cc.find("PebmanentNode").getComponent("UserInfo").LoadTwoUser(cc.find("PebmanentNode").getComponent("UserInfo").pictureUser,
        cc.find("PebmanentNode").getComponent("UserInfo").pictureUser,cc.find("Canvas/UserName"),cc.find("PebmanentNode").getComponent("UserInfo").nameUser,cc.find("Canvas/UserPicture1"),cc.find("Canvas/UserPicture"));
    },
    start () {

    },
    backGame : function(){
        cc.audioEngine.play(this.clickAudio,false,1);
        //
        var jsonData = {
            tag1            : 14,
            score           : "",
            type            : "",
            state1          : "",
            changeMapList1  : [],
            removeMapList1  : 0,
            nMapRow         : "",
            nMapCol         : "",
            loading1        : "",
        }
        cc.find("PebmanentNode").getComponent("GetServer").SendLongMsg(jsonData);
        cc.director.loadScene("PersonsChoose");
    },
    update (dt) {
        //
        console.log("--->>>>>>this.matchSuccess is ",this.matchSuccess);
        if(this.matchSuccess)
        {
            console.log("%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%");
            //back
            this.backBtn.active = false;
            if(cc.find("PebmanentNode").getComponent("UserInfo").rivalName !== "")
                this.rivalNameLabel.string = cc.find("PebmanentNode").getComponent("UserInfo").rivalName;
            this.node.getChildByName("countDown").active = true;
            //
            this.showCountDown += dt;
            this.nSuccessTime +=dt;
            if(this.showCountDown >= 1 && this.startTime != 0){
                this.playAudio(this.countDownAudio);
                this.node.getChildByName("countDown").getComponent(cc.Label).string = --this.startTime + "s";
                this.showCountDown = 0;
            }
            if(this.nSuccessTime>=5)
            {
                //
                var gameType = cc.find("PebmanentNode").getComponent("UserInfo").nGameType;
                console.log("gameType is " + gameType);
                // gameType = gameType.toString();
                console.log(typeof gameType);
                switch(gameType){
                    //
                    case "1":
                        console.log("");
                        this.nSuccessTime = 0;
                        cc.director.loadScene("PersonsGame1");
                        break;
                        //    
                    case "2":
                        this.nSuccessTime = 0;
                        cc.director.loadScene("FightStone");
                        break;
                    case "3":
                        break;
                        //
                    case "4":
                        this.nSuccessTime = 0;
                        cc.director.loadScene("FightFigure");
                        //
                        break;
                }
                
            }
        }
    },
    //
    playAudio : function(audioUrl){
        cc.audioEngine.play(audioUrl,false,1);
    },
    //
    share : function(){
        if(CC_WECHATGAME){
            console.log("share");
            cc.find("PebmanentNode").getComponent("UserInfo").shareFriends();
        }else if(cc.sys.isNative){
            //
            cc.find("PebmanentNode").getComponent("UserInfo").nativeShare();
        }
    }
});
