                                // Global.game1Main.groundChild[nArrayList[l] - 1][nArrayRow[l] - 1].getComponent("PrefabState").isBox = false;
                                this.arrayChangeNode.push({ "row": nArrayRow[l] - 2, "col": nArrayList[l] - 1, "color": this.stringColor });
