    //
    /**
     * @param  {} buttonNode
     * @param  {} worldPosition
     */
    getTouchLine : function(buttonNode,worldPosition){
        //x
        var localX = buttonNode.convertToNodeSpaceAR(cc.v2(worldPosition.x,worldPosition.y)).x;
        cc.log("local is " + localX);
        if(localX > 0){
             var n1 = Math.floor(localX/(buttonNode.width/6));
             if(n1 >= 0 && n1 < 1){
                this.nCol = 3;
             }else if(n1 >= 1 && n1 < 2){
                 this.nCol = 4;
             }else if(n1 >= 2 && n1 < 3){
                 this.nCol = 5;
             }
        }else if(localX < 0){
            var n2 = Math.floor(-localX/(buttonNode.width/6));
            if(n2 >= 0 && n2 < 1){
               this.nCol = 2;
           }else if(n2 >= 1 && n2 < 2){
               this.nCol = 1;
           }else if(n2 >= 2 && n2 < 3){
               this.nCol = 0;
           }
        }
        if(this.nCol >= 5){
            this.nCol = 5;
        }
        return this.nCol;
   },
    onKeyDown : function(event){
        switch(event.keyCode){
            case cc.KEY.down:
                this.xltime = this.quickSpeed;
                 break;
            case cc.KEY.left:
                 this.moveLeft();
                 cc.log("<-----");
                 break;
            case cc.KEY.right:
                 this.moveRight();
                 cc.log("----->");
                 break;  
            case cc.KEY.f:
                 this.rotate();
                 //
                 break;                  
        }
    },
    onKeyUp   : function(event){
        switch(event.keyCode){
            case cc.KEY.down:
                this.xltime = this.normalSpeed;
                 break;
            case cc.KEY.left:
                 break;
            case cc.KEY.right:
                 break;  
            case cc.KEY.f:
                 //
                 break;                 
        }
    },
