cc.Class({
    extends: cc.Component,

    properties: {
        nickName : cc.Node,
        icon     : cc.Node,
        clickAudio :{
            url : cc.AudioClip,
            default : null,
        },
    },
    

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        if(CC_WECHATGAME){
            //
            cc.find("PebmanentNode").getComponent("UserInfo").gameClubButton.hide();
        }
        // //
        cc.director.preloadScene("Game1",function(){
            cc.log("");
        });
        cc.director.preloadScene("Game2",function(){
            cc.log("");
        });
        // cc.director.preloadScene("Game3",function(){
        //     cc.log("");
        // });
        cc.director.preloadScene("Game4",function(){
            cc.log("");
            console.log(":",cc.find("PebmanentNode"));
            if(cc.find("PebmanentNode") !== null){
                //
                cc.find("PebmanentNode").getComponent("UserInfo").showAdvice("adunit-4c58e1d36d38a2d6");
            }
            
        });
        // let textureListLength = cc.textureCache.getAllTextures().length;
        // for(let i = 0;i <textureListLength;i++){
        //     console.log("in oneChooseModel's all textures is ",cc.textureCache.getAllTextures()[i].url);
        // }
    },
    
    start () {
        if(cc.sys.isNative){
            var userInfoScript = cc.find("PebmanentNode").getComponent("UserInfo");
            this.nickName.getComponent(cc.Label).string = userInfoScript.nameUser;
            userInfoScript.LoadUserPicture(userInfoScript.pictureUser,this.icon);
            //
            cc.find("PebmanentNode").getComponent("UserInfo").showUsername(userInfoScript.nameUser,this.nickName);
        }
        if(CC_WECHATGAME){
            var userInfoScript = cc.find("PebmanentNode").getComponent("UserInfo");
            console.log("in chooseModel nameUser is ",userInfoScript.nameUser);
            // this.nickName.getComponent(cc.Label).string = userInfoScript.nameUser;
            cc.find("PebmanentNode").getComponent("UserInfo").loadUserPictureByWx(cc.find("PebmanentNode").getComponent("UserInfo").pictureUser,cc.find("Canvas/icon"));
            //
            cc.find("PebmanentNode").getComponent("UserInfo").showUsername(userInfoScript.nameUser,this.nickName);
        }
        
    },
    //
    figureBlock : function(){
        if(CC_WECHATGAME){
            wx.showLoading({
                title : '...',
                mask  : true,
            })
        }
        this.playClickMusic();
        cc.director.loadScene("Game4",function(){
            if(CC_WECHATGAME){
               //
               wx.hideLoading();
            }
        });
    },
    //
    puyoBlock   : function(){
        // this.playClickMusic();
        // cc.director.loadScene("Game2");
    },
    //
    tetrisBlock : function(){
        if(CC_WECHATGAME){
            wx.showLoading({
                title : '...',
                mask  : true,
            })
        }
        this.playClickMusic();
        cc.director.loadScene("Game1",function(){
            if(CC_WECHATGAME){
               //
               wx.hideLoading();
            }
        });
    },
    //
    stoneBlock  : function(){
        if(CC_WECHATGAME){
            wx.showLoading({
                title : '...',
                mask  : true,
            })
        }
        this.playClickMusic();
        cc.director.loadScene("Game3",function(){
            if(CC_WECHATGAME){
               //
               wx.hideLoading();
            }
        });
    },
    //
    back        : function(){
        if(CC_WECHATGAME){
            wx.showLoading({
                title : '...',
                mask  : true,
            })
        }
        this.playClickMusic();
        cc.director.loadScene("Main",function(){
            if(CC_WECHATGAME){
                wx.hideLoading();
            }
        });
    },
    share : function(){
        if(CC_WECHATGAME){
            console.log("share");
            cc.find("PebmanentNode").getComponent("UserInfo").shareFriends();
        }else if(cc.sys.isNative){
            //
            cc.find("PebmanentNode").getComponent("UserInfo").nativeShare();
        }
    },
    playClickMusic : function(){
        cc.audioEngine.play(this.clickAudio,false,1);
    },
    // update (dt) {},
    
});
