//
nativeShare : function(){
    //   
        //   
        if (cc.sys.isBrowser) {  
            cc.log('~');  
            return;  
        }  
  
        //   
        if (this._isCapturing) {  
            return;  
        }  
        this._isCapturing = true;  
  
        //   
        var fileName = "shareScreenshot.jpg";  
        var fullPath = jsb.fileUtils.getWritablePath() + fileName;  
        if (jsb.fileUtils.isFileExist(fullPath)) {  
            jsb.fileUtils.removeFile(fullPath);  
        }  
  
        //   
        var size = cc.director.getWinSize(); //   
        var texture = new cc.RenderTexture(size.width, size.height); //   
        texture.setPosition(cc.p(size.width / 2, size.height / 2)); //   
        texture.begin(); //   
        cc.director.getRunningScene().visit(); //   
        texture.end(); //   
        texture.saveToFile(fileName, cc.IMAGE_FORMAT_JPG); //   
        console.log("");  
        // 50  
        // 1010  
        var self = this;  
        var tryTimes = 0;  
        var fn = function () {  
            if (jsb.fileUtils.isFileExist(fullPath)) {  
                //   
                if (cc.sys.os === cc.sys.OS_ANDROID) {  
                    jsb.reflection.callStaticMethod('org/cocos2dx/javascript/AppActivity', 'shareIMG', '(Ljava/lang/String;)V', fullPath);  
                    console.log("");  
                } else if (cc.sys.os === cc.sys.OS_IOS) {  
                    jsb.reflection.callStaticMethod('AppController', 'shareIMG:', fullPath);  
                }  
                self._isCapturing = false;  
            } else {  
                tryTimes++;  
                if (tryTimes > 10) {  
                    self._isCapturing = false;  
                    cc.log('~');  
                    return;  
                }  
                setTimeout(fn, 50);  
            }  
        };  
        setTimeout(fn, 50);  
        console.log("");  
},
