/**
 * @param  {} adId
 */
showAdvice : function(adId){
        var self = this;
        let bannerHeight = 60;
        let bannerWidth = 300;
        if(CC_WECHATGAME){
            let winSize = wx.getSystemInfoSync();
            console.log("winSize is ",winSize);
            console.log("advice is ",Global.advertisement);
            console.log("adId is ",adId);
            if(Global.advertisement === null){
                this.createAdvertisement(winSize,bannerWidth,bannerHeight,adId);
            }else{
                Global.advertisement.destroy();
                this.createAdvertisement(winSize,bannerWidth,bannerHeight,adId);
            }
        }
},
createAdvertisement : function(winSize,bannerWidth,bannerHeight,adId){
    var self = this;
    console.log("winSize is ",winSize);
    if(CC_WECHATGAME){
        Global.advertisement = wx.createBannerAd({
            adUnitId  : adId,
            style     : {
                left  : 0,
                top   : winSize.windowHeight - bannerHeight,
                width : bannerWidth,
            }
        });
        // let bannerAd = wx.createBannerAd({
        //     adUnitId: 'adunit-e8fb34802309ceff'
        //     style: {
        //         left: 0,
        //         top: 0,
        //         width: 350
        //     }
        // })
        let ad = Global.advertisement;
        ad.show();
        console.log("advice is ",ad);
        ad.onLoad(()=>{
            console.log("banner ");
        });
        ad.onError(err=>{
            console.log("err is ",err);
        });
        //
        ad.onResize(res =>{
            console.log("",ad);
            console.log("res is ",res);
            console.log(res.width,res.height);
            console.log(ad.style.realWidth,ad.style.realHeight);
            console.log("realHeight is ",ad.style.realHeight);
            ad.style.top = winSize.windowHeight - ad.style.realHeight;
            ad.style.width = winSize.windowWidth;
        });
    }
},
