    /**
     * @param  {Number} nMaxDisappea 
     * @param  {Number} nDisappearAll 
     */
    BoxDown: function (nMaxDisappea, nDisappearAll) {
        for (var i = nMaxDisappea + 1; i <= 19; i++) {
            for (var j = 0; j <= 9; j++) {
                if (this.groundChild[j][i].getComponent("PrefabState").isBox) {
                    for (var l = 0; l <= this.boxParent1.length - 1; l++) {
                        if (this.boxParent1[l].y == this.groundChild[j][i].y && this.boxParent1[l].x == this.groundChild[j][i].x) {
                            this.boxParent1[l].setPosition(cc.p(this.groundChild[j][i].x, this.groundChild[j][i].y - Global.nWidth * nDisappearAll));
                        }
                    }
                    //
                    this.groundChild[j][i].getComponent("PrefabState").isBox = false;
                    //true
                    this.groundChild[j][i - nDisappearAll].getComponent("PrefabState").isBox = true;
                }
            }
        }
    },
    //isBoxtrue,
    DisappearBox: function () {
        //
        var nDisappear = [];
        for (var i = 0; i <= 19; i++) {
            var nTrue = 0;
            for (var j = 0; j <= 9; j++) {
                // if (j == 0) {
                //     //itrue
                //     nTrue = 0;
                // }
                if (this.groundChild[j][i].getComponent("PrefabState").isBox) {
                    nTrue++;
                }
                if (j == 9) {
                    for (var a = 0; a <= 9; a++) {
                        console.log(this.groundChild[a][i].getComponent("PrefabState").isBox);
                    }
                    if (nTrue == 10) {
                        console.log("ppppppppppppppppppppppppppppppppppppp");
                        this.playAudio(this.removeAudio);
                        //isBoxtrue
                        this.TraversalNodeBox(this.groundChild[j][i].y);
                        //isBoxfalse
                        for (var k = 0; k <= 9; k++) {
                            this.groundChild[k][i].getComponent("PrefabState").isBox = false;
                        }
                        nDisappear.push(i);
                    }
                    if (i == 19 && nDisappear.length != 0) {
                        console.log("",nDisappear.length);
                        //1
                        if (nDisappear.length == 1) {
                            //
                            this.BoxDown(nDisappear[0], nDisappear.length);
                        }
                        else {
                            // 
                            for (var k = 0; k <= nDisappear.length - 1; k++) {
                                for (var l = k + 1; l <= nDisappear.length - 1; l++) {
                                    if (k <= 2) {
                                        if (nDisappear[l] < nDisappear[k]) {
                                            var nMin = nDisappear[k];
                                            nDisappear[k] = nDisappear[l];
                                            nDisappear[l] = nMin;
                                        }
                                    }
                                }
                            }
                            console.log("",nDisappear.length);
                            // let disappearLength = nDisappear.length;
                            // nDisappear.forEach(element=>{
                            //     this.BoxDown(element,nDisappear);
                            // })
                            //
                            this.BoxDown(nDisappear[nDisappear.length - 1], nDisappear.length);
                        }
                        this.ShowScore(nDisappear.length, this.nodeScore1); 
                        console.log(nDisappear.length + "DisappearBox");
                        this.disappearAll += nDisappear.length;
                    }
                }
            }
        }
    },
    //
    GetBlockLine: function () {
        var nLine = (this.nodeBlock.x - this.groundParent.x) / Global.nWidth + 1;
        return nLine;
    },
    getTouchLine : function(buttonNode,worldPosition){
        console.log("");
        //x
        var localX = buttonNode.convertToNodeSpaceAR(cc.v2(worldPosition.x,worldPosition.y)).x;
        cc.log("local is " + localX);
        if(localX > 0){
             var n1 = Math.floor(localX/(buttonNode.width/10));
             if(n1 >= 0 && n1 < 1){
                this.nCol = 6;
             }else if(n1 >= 1 && n1 < 2){
                 this.nCol = 7;
             }else if(n1 >= 2 && n1 < 3){
                 this.nCol = 8;
             }else if(n1 >= 3 && n1 < 4){
                 this.nCol = 9;
             }else if(n1 >= 4 && n1 < 5){
                 this.nCol = 10;
             }
        }else if(localX < 0){
            var n2 = Math.floor(-localX/(buttonNode.width/10));
            if(n2 >= 0 && n2 < 1){
               this.nCol = 5;
           }else if(n2 >= 1 && n2 < 2){
               this.nCol = 4;
           }else if(n2 >= 2 && n2 < 3){
               this.nCol = 3;
           }else if(n2 >= 3 && n2 < 4){
               this.nCol = 2;
           }else if(n2 >= 4 && n2 < 5){
               this.nCol = 1;
           }
        }
        if(this.nCol >= 10){
            this.nCol = 10;
        }
        if(this.nCol <= 1){
            this.nCol = 1;
        }
        return this.nCol;
   },
    //
    GetTouchLine: function (worldPosition) {
        //
        var nodePosition = worldPosition - (this.nodeMove1.x + this.nodeCanvas.x);
        if (nodePosition > 0) {
            var nWidth1 = Math.floor(nodePosition / (this.nodeMove1.width / 20));
            if (nWidth1 == 0 || nWidth1 == 1) {
                this.nLine = 6;
            }
            else if (nWidth1 == 2 || nWidth1 == 3) {
                this.nLine = 7;
            }
            else if (nWidth1 == 4 || nWidth1 == 5) {
                this.nLine = 8;
            }
            else if (nWidth1 == 6 || nWidth1 == 7) {
                this.nLine = 9;
            }
            else {
                this.nLine = 10;
            }

            return this.nLine;
        }
        if (nodePosition < 0) {
            var nWidth1 = Math.floor((-nodePosition) / (this.nodeMove1.width / 20));
           
            if (nWidth1 == 0 || nWidth1 == 1) {
                this.nLine = 5;

            }
            else if (nWidth1 == 2 || nWidth1 == 3) {
                this.nLine = 4;

            }
            else if (nWidth1 == 4 || nWidth1 == 5) {
                this.nLine = 3;

            }
            else if (nWidth1 == 6 || nWidth1 == 7) {
                this.nLine = 2;
            }
            else {
                this.nLine = 1;
            }
            return this.nLine;
        }
    },
